import { mapState, mapActions, mapMutations } from 'vuex'

export default {
  data() {
    return {
      active:'分类1',
      classid:'0'
    };
  },
  computed: {
    ...mapState({
      goodsLists:state => state.shop.goodsLists,
    })
  },
  mounted() {
  },
  methods: {
    changeid(index){
       this.classid=index.toString();
    },
    goDetail(data){
      this.$store.commit("setDeatils",data);
      this.$store.commit("handleClick");
    }
  }
};



